@import "https://fonts.googleapis.com/css?family=Open+Sans|Roboto:300";

.container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Roboto", sans-serif;

  background: #757575;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 300px;
  text-align: center;
  background: #3b3b3b;

  border-radius: 5%;
  margin: 30px 0;
}

.avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid #19f6e8;
  margin: 30px 0;
}

.name {
  color: #19f6e8;

  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-size: 1.4em;
  font-weight: normal;

  line-height: 32px;
  letter-spacing: 4px;

  margin-bottom: 5px;
}

.title {
  color: #fff;
  font-size: 12px;
  letter-spacing: 2px;
}

.icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  margin-bottom: 20px;
  margin-top: 10px;
}

button:hover,
a:hover {
  opacity: 0.7;
}
